.order {
    display: flex;
    flex-direction: column;
    align-items: flex-start;

    width: 100%;

    padding: 40px;
    margin-bottom: 20px;

    border-radius: 5px;

    box-shadow: 0px 0px 20px 0px lightgray;
    background-color: white;
}

.order .name {
    color: #383838;
    font-size: 20px;
    font-weight: 700;

    margin-bottom: 5px;
}

.order .date {
    color: rgba(36, 14, 14, 0.3);
    font-size: 16px;
    font-weight: 700;
}

.order .data {
    width: 100%;

    display: flex;
    flex-direction: row;
    align-items: flex-start;
}

.order .data .user-info {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 6px 0px;

    margin: 20px 100px 0px 0px;
}

.order .data .user-info .heading {
    color: #383838;
    font-size: 16px;
    font-weight: 700;
}

.order .data .user-info .info {
    color: #383838;
    font-size: 16px;
    font-weight: 400;
}

.order .data .products {
    flex: 1;
    width: 100%;

    margin: 20px 0px 0px 0px;

    overflow-x: scroll;
}

.order .data .products .slider {
    display: flex;
    flex-direction: row;
}

.order .data .products .slider .product {
    display: flex;
    flex-direction: column;
    align-items: center;
    
    width: 150px;

    margin: 10px;
}

.order .data .products .slider .product .image {
    width: 150px;
    height: 150px;
    object-fit: contain;

    margin-bottom: 10px;
}

.order .data .products .slider .product .name {
    color: #383838;
    font-size: 16px;
    font-weight: 700;
}

.order .data .products .slider .product .amount {
    color: #383838;
    font-size: 16px;
    font-weight: 700;
}

.order .button-panel {
    display: flex;
    flex-direction: row;
    align-items: center;

    margin-top: 40px;
}

.order .button-panel .button-filled {
    margin-right: 15px;
}