#blog {
    width: 100%;

    display: flex;
    flex-direction: column;
    align-items: center;
}

@media (min-width: 1600px) {
    #blog .content {
        display: flex;
        flex-direction: column;
    
        width: 1600px;
    
        padding: 100px 150px;
    }
}

@media (max-width: 1600px) {
    #blog .content {
        display: flex;
        flex-direction: column;

        width: 100%;

        padding: 100px 150px;
    }
}

@media (max-width: 1300px) {
    #blog .content {
        padding: 50px 50px;
    }
}

@media (max-width: 800px) {
    #blog .content {
        padding: 20px;
    }
}

#blog .content .article-panel {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 60px 30px;
}

@media (max-width: 900px) {
    #blog .content .article-panel {
        grid-template-columns: repeat(2, 1fr);
        gap: 30px;
    }
}

@media (max-width: 600px) {
    #blog .content .article-panel {
        grid-template-columns: repeat(1, 1fr);
        gap: 30px;
    }
}

#blog .content .article-panel .article {
    display: flex;
    flex-direction: column;
    align-items: flex-start;

    border-radius: 5px;

    transition: all 200ms;

    cursor: pointer;

    text-decoration: none;

    box-shadow: 0px 0px 20px -10px gray;
    background-color: white;
}

#blog .content .article-panel .article:hover {
    box-shadow: 0px 0px 30px -15px black;
}

#blog .content .article-panel .article .image {
    width: 100%;
    height: 200px;
    object-fit: cover;

    border-radius: 5px 5px 0px 0px;
}

#blog .content .article-panel .article .date {
    color: rgba(0, 0, 0, 0.3);
    font-size: 12px;
    font-weight: 700;

    margin: 20px 20px 0px 20px;
}

#blog .content .article-panel .article .title {
    color: #383838;
    font-size: 20px;
    font-weight: 700;

    margin: 5px 20px 10px 20px;
}

#blog .content .article-panel .article .text {
    color: #383838;
    font-size: 15px;
    font-weight: 400;

    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;

    margin: 0px 20px 20px 20px;
}