#admin-shop {
    width: 100%;

    background-color: white;
}

#admin-shop .content {
    width: 100%;

    display: flex;
    flex-direction: column;
    align-items: flex-start;

    padding: 50px;
}

@media (max-width: 800px) {
    #admin-shop .content {
        padding: 20px;
    }
}

#admin-shop .content .title {
    color: #383838;
    font-size: 40px;
    font-weight: 700;

    margin-bottom: 50px;
}

#admin-shop .content .section {
    color: #383838;
    font-size: 20px;
    font-weight: 700;

    width: 100%;

    padding-bottom: 5px;
    margin-bottom: 10px;

    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}

#admin-shop .content .text {
    color: rgba(0, 0, 0, 0.5);
    font-size: 16px;
    font-weight: 400;
    line-height: 26px;

    width: 50%;

    margin-bottom: 50px;
}

@media (max-width: 800px) {
    #admin-shop .content .text {
        width: 100%;
    }
}

#admin-shop .content .details {
    display: grid;
    grid-template-columns: auto auto;
    gap: 15px 20px;

    margin-bottom: 50px;

    align-items: start;
    justify-items: start;
}

@media (max-width: 800px) {
    #admin-shop .content .details {
        grid-template-columns: auto;

        width: 100%;
    }
}

#admin-shop .content .details .heading {
    color: #383838;
    font-size: 16px;
    font-weight: 700;

    margin: 0px;
    padding: 0px;
    margin-top: 8px;
    padding-right: 50px;
}

@media (max-width: 800px) {
    #admin-shop .content .details .heading {
        margin: 0px;
        padding: 0px;
    }
}

#admin-shop .content .details .field {
    color: #383838;
    font-size: 15px;
    font-weight: 400;

    margin: 0px;
    padding: 0px;
    padding: 8px;

    width: 400px;

    border: 1px solid lightgray;
    outline: none;
}

#admin-shop .content .details .select {
    color: #383838;
    font-size: 15px;
    font-weight: 400;

    margin: 0px;
    padding: 0px;
    padding: 8px;

    border: 1px solid lightgray;
    outline: none;
}

#admin-shop .content .details .area {
    color: #383838;
    font-size: 15px;
    font-weight: 400;
    line-height: 25px;

    margin: 0px;
    padding: 0px;
    padding: 8px;

    width: 600px;

    border: 1px solid lightgray;
    outline: none;
    resize: none;
}

@media (max-width: 700px) {
    #admin-shop .content .details .field {
        width: 100%;
    }

    #admin-shop .content .details .area {
        width: 100%;
    }

    #admin-shop .content .details .select {
        width: 100%;
    }
}

#admin-shop .content .details .problem-list {
    width: 900px;

    display: grid;
    grid-template-columns: repeat(5, 1fr);
    gap: 20px;
}

#admin-shop .content .details .problem-list .item {
    color: #383838;
    font-size: 18px;
    font-weight: 700;
    text-align: center;

    display: flex;
    align-items: center;
    justify-content: center;

    padding: 20px;

    cursor: pointer;

    transition: all 100ms;

    background-color: rgba(0, 0, 0, 0.07);
}

#admin-shop .content .details .problem-list .item:hover {
    background-color: rgba(0, 0, 0, 0.14);
}



#admin-shop .content .file-input {
    display: none;
}

#admin-shop .content .image {
    width: 250px;

    margin: 20px 0px 60px 0px;
}

/* -----------------------------------------*/

#admin-orders {
    width: 100%;

    background-color: white;
}

#admin-orders .content {
    width: 100%;

    display: flex;
    flex-direction: column;
    align-items: flex-start;

    padding: 50px;
}

@media (max-width: 800px) {
    #admin-orders .content {
        padding: 20px;
    }
}

#admin-orders .content .title {
    color: #383838;
    font-size: 40px;
    font-weight: 700;

    margin-bottom: 20px;
}

#admin-orders .content .text {
    color: rgba(0, 0, 0, 0.5);
    font-size: 16px;
    font-weight: 400;
    line-height: 26px;

    width: 50%;
}

#admin-orders .content .menu {
    display: flex;
    flex-direction: row;

    margin: 40px 0px;
}

#admin-orders .content .menu .item {
    color: #383838;
    font-size: 16px;
    font-weight: 700;

    border-radius: 1000px;

    padding: 10px 20px;
    margin: 0px 5px;
    
    cursor: pointer;

    transition: all 200ms;
}

#admin-orders .content .menu .item:hover {
    background-color: rgba(0, 0, 0, 0.05);
}

#admin-orders .content .orders {
    display: flex;
    flex-direction: column;
    
    width: 100%;
}

#admin-orders .content .empty-message {
    color: rgba(0, 0, 0, 0.5);
    font-size: 18px;
    font-weight: 400;

    margin-top: 20px;
}