#payment {
    width: 100%;

    display: flex;
    flex-direction: column;
}

#payment .content {
    display: flex;
    flex-direction: column;
    align-items: flex-start;

    padding: 50px 100px;
}

@media (max-width: 1300px) {
    #payment .content {
        padding: 50px;
    }
}

@media (max-width: 800px) {
    #payment .content {
        padding: 50px 20px;
    }
}

#payment .content .title {
    color: #383838;
    font-size: 40px;
    font-weight: 700;

    margin-bottom: 10px;
}

#payment .content .description {
    color: rgba(0, 0, 0, 0.5);
    font-size: 18px;
    font-weight: 400;
    line-height: 30px;

    width: 70%;

    margin-bottom: 50px;
}

@media (max-width: 800px) {
    #payment .content .description {
        width: 100%;
    }
}

#payment .content .summary {
    border-collapse: collapse;

    margin-bottom: 50px;
}

#payment .content .summary .product {
    
}

#payment .content .summary tr td {
    color: #383838;
    font-size: 18px;
    font-weight: 700;

    padding: 15px 20px;
}

@media (max-width: 800px) {
    #payment .content .summary tr td {
        font-size: 13px;

        padding: 10px;
        text-align: center;
    }
}

@media (max-width: 320px) {
    #payment .content .summary tr td {
        font-size: 10px;
    }
}

#payment .content .summary .product .product-td {
    color: #383838;
    font-size: 18px;
    font-weight: 400;

    padding: 10px 20px;

    border: 1px solid rgba(0, 0, 0, 0.05);
}

@media (max-width: 800px) {
    #payment .content .summary .product .product-td {
        font-size: 13px;

        padding: 10px;
        text-align: center;
    }
}

@media (max-width: 320px) {
    #payment .content .summary .product .product-td {
        font-size: 10px;
    }
}

#payment .content .summary .product .image {
    height: 60px;
}

@media (max-width: 800px) {
    #payment .content .summary .product .image {
        height: 40px;
    }
}