.title-panel {
    width: 100%;
    height: 300px;

    display: flex;
    align-items: center;
    justify-content: center;

    position: relative;
}

.title-panel .image {
    position: absolute;
    left: 0px;
    top: 0px;

    width: 100%;
    height: 100%;

    object-fit: cover;

    filter: brightness(80%);
}

.title-panel .title {
    color: white;
    font-size: 50px;
    font-weight: 700;
    text-align: center;

    z-index: 2;
}