#shop {
    display: flex;
    flex-direction: column;
}

#shop .content {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
}

#shop .content .product-panel {
    display: flex;
    flex-direction: column;

    width: 100%;

    flex: 1;
}

#shop .content .product-panel .upper-panel {
    padding: 50px 100px 30px 100px;
}

@media (max-width: 1300px) {
    #shop .content .product-panel .upper-panel {
        padding: 50px;
    }
}

@media (max-width: 800px) {
    #shop .content .product-panel .upper-panel {
        padding: 50px 20px;
    }
}

#shop .content .product-panel .upper-panel .title {
    color: #383838;
    font-size: 40px;
    font-weight: 700;

    margin-bottom: 20px;
}

@media (max-width: 800px) {
    #shop .content .product-panel .upper-panel .title {
        font-size: 30px;
    }
}

#shop .content .product-panel .upper-panel .text {
    color: rgba(0, 0, 0, 0.5);
    font-size: 18px;
    font-weight: 400;
    line-height: 30px;

    width: 70%;
}

@media (max-width: 1000px) {
    #shop .content .product-panel .upper-panel .text {
        width: 100%;
    }
}

#shop .content .product-panel .filters {
    display: flex;
    flex-direction: row;
    align-items: center;

    padding: 0px 100px;
}

@media (max-width: 1300px) {
    #shop .content .product-panel .filters {
        display: flex;
        flex-direction: row;
        align-items: center;

        padding: 0px 50px;
    }
}

@media (max-width: 800px) {
    #shop .content .product-panel .filters {
        padding: 0px 20px;

        flex-direction: column;
    }
}

#shop .content .product-panel .filters .field {
    color: #383838;
    font-size: 15px;
    font-weight: 400;

    padding: 8px;
    margin: 0px;
    margin-right: 20px;

    width: 250px;

    border: 1px solid lightgray;
    outline: none;
}

@media (max-width: 800px) {
    #shop .content .product-panel .filters .field {
        width: 100%;

        margin: 0px;
        margin-bottom: 20px;
    }
}

#shop .content .product-panel .filters .button-filled {
    align-self: center;
}

@media (max-width: 800px) {
    #shop .content .product-panel .filters .button-filled {
        width: 100%;
    }
}

#filters-button {
    display: none;
}

@media (max-width: 800px) {
    #filters-button {
        display: inline-block;

        margin: 20px;
    }
}

#shop .content .product-panel .products {
    width: 100%;

    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 60px 80px;

    padding: 50px 100px;

    margin-bottom: 30px;
}

@media (max-width: 1400px) {
    #shop .content .product-panel .products {
        gap: 60px 20px;

        padding: 50px;
    }
}

@media (max-width: 1200px) {
    #shop .content .product-panel .products {
        grid-template-columns: repeat(3, 1fr);
    }
}

@media (max-width: 1000px) {
    #shop .content .product-panel .products {
        grid-template-columns: repeat(2, 1fr);
        gap: 40px 20px;

        padding: 50px 20px;
        padding-top: 20px;
    }
}

#shop .content .product-panel .products .product {
    display: flex;
    flex-direction: column;

    text-decoration: none;

    position: relative;
}

#shop .content .product-panel .products .product .image {
    width: 100%;
    height: 230px;
    object-fit: contain;

    padding: 30px 0px;
    margin-bottom: 10px;

    background-color: rgba(0, 0, 0, 0.03);
}

#shop .content .product-panel .products .product .top-12 {
    position: absolute;
    top: -10px;
    left: -10px;

    width: 50px;
    height: 50px;

    z-index: 2;
}

#shop .content .product-panel .products .product .name {
    color: #383838;
    font-size: 20px;
    font-weight: 700;
}

#shop .content .product-panel .products .product .description {
    color: rgba(0, 0, 0, 0.4);
    font-size: 15px;
    font-weight: 400;

    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
}

@media (min-width: 700px) {
    #shop .content .product-panel .products .product .panel {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;

        margin-top: 15px;
    }

    #shop .content .product-panel .products .product .panel .price {
        color: #383838;
        font-size: 18px;
        font-weight: 700;
    }
}

@media (max-width: 700px) {
    #shop .content .product-panel .products .product .panel {
        display: flex;
        flex-direction: column;
        align-items: flex-start;

        margin-top: 15px;
    }

    #shop .content .product-panel .products .product .panel .price {
        color: #383838;
        font-size: 20px;
        font-weight: 700;

        margin-bottom: 15px;
    }
}

#shop .content .product-panel .products .product .panel .sold-message {
    color: #A161B3;
    font-size: 18px;
    font-weight: 700;
}

#shop .content .product-panel .products .product .panel .button-filled {
    font-size: 13px;
    font-weight: 400;
    padding: 6px 15px;
}

#shop .content .product-panel .empty-message {
    width: 100%;

    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;

    padding: 75px 100px;

    color: #383838;
    font-size: 20px;
    font-weight: 400;
}

@media (max-width: 800px) {
    #shop .content .product-panel .empty-message {
        align-items: flex-start;

        padding: 50px 20px;
    }
}

#more-button {
    align-self: center;

    margin-bottom: 50px;
}

#shop .content .category-panel {
    background-color: white;
}

#categories {
    display: flex;
    flex-direction: column;

    padding-bottom: 30px;
}

#categories .title {
    color: #383838;
    font-size: 20px;
    font-weight: 700;

    margin: 30px;
    margin-bottom: 15px;
}

#categories .title ion-icon {
    display: none;
}

#categories .list {
    display: flex;
    flex-direction: column;
}

#categories .list .item {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;

    width: 300px;

    color: #383838;
    font-size: 16px;

    padding: 10px 10px 10px 30px;

    cursor: pointer;

    transition: all 100ms;
}

#categories .list .item ion-icon {
    color: rgba(140, 140, 140)
}

#categories .list .item:hover {
    background-color: rgb(240, 240, 240);
}

#categories .list .dropdown {
    transition: all 500ms ease-in-out;
    height: auto;

    overflow: hidden;
}

#categories .list .dropdown .item {
    padding-left: 50px;
}

#categories .list .item .checkbox {
    width: 12px;
    height: 12px;

    border-radius: 50%;

    margin-right: 15px;

    transition: all 100ms;

    background-color: rgba(220, 220, 220);
}

@media (max-width: 800px) {
    #shop .content .category-panel {
        display: flex;

        position: fixed;
        left: 0px;
        top: 0px;
        width: 100vw;
        height: 100vh;

        overflow-y: scroll;

        z-index: 999;
    }
    
    #categories {
        display: flex;
        flex-direction: column;
    
        width: 100%;
    }
    
    #categories .title {
        color: #383838;
        font-size: 30px;
        font-weight: 700;

        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
    
        margin: 30px;
    }

    #categories .title ion-icon {
        display: inline-block;

        cursor: pointer;

        color: rgba(140, 140, 140);
    }
    
    #categories .list {
        display: flex;
        flex-direction: column;

        width: 100%;
    }
    
    #categories .list .item {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;

        color: #383838;
        font-size: 20px;

        width: 100%;
    
        padding: 10px 30px 10px 30px;
    
        cursor: pointer;
    
        transition: all 100ms;
    }
    
    #categories .list .item ion-icon {
        color: rgba(140, 140, 140);

        width: 25px;
        height: 25px;
    }
    
    #categories .list .item:hover {
        background-color: rgb(240, 240, 240);
    }
    
    #categories .list .dropdown {
        transition: all 500ms ease-in-out;
        height: auto;
    
        overflow: hidden;
    }
    
    #categories .list .dropdown .item {
        padding-left: 50px;
    }
    
    #categories .list .item .checkbox {
        width: 12px;
        height: 12px;
    
        border-radius: 50%;
    
        margin-right: 15px;
    
        transition: all 100ms;
    
        background-color: rgba(220, 220, 220);
    }
}