@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@300;400;700;900&display=swap);
@media (min-width: 1320px) {
    #menu-button {
        display: none;
    }

    #mobile-menu {
        display: none;
    }
}

@media (max-width: 1320px) {
    #menu-button {
        width: 28px;
        height: 17px;

        display: flex;
        flex-direction: column;
        align-items: flex-end;
        justify-content: space-between;

        margin-right: 30px;
    }

    #menu-button .line {
        width: 100%;
        height: 3px;
        border-radius: 10px;

        background-color: #383838;
    }

    #menu-line-1, #menu-line-2, #menu-line-3 {
        transition: all 400ms;
    }

    #mobile-menu {
        display: flex;
        flex-direction: column;
        align-items: flex-end;
        justify-content: flex-start;

        position: fixed;
        right: -100vw;
        top: 0;

        height: 100%;
        height: -moz-available;
        height: -webkit-fill-available;
        height: fill-available;

        padding: 30px 30px 30px 60px;

        transition: right 700ms;

        box-shadow: 0px 0px 15px -10px black;
        background-color: white;

        z-index: 9;
    }

    #mobile-menu .link {
        color: #383838;
        font-size: 26px;
        font-weight: 700;
        text-align: center;
        text-decoration: none;

        padding: 6px;
    }
}
#dropdown {
    position: fixed;
    left: 0px;
    top: 0px;

    width: 100%;

    display: flex;
    flex-direction: column;

    padding: 30px 50px;

    background-color: white;
    box-shadow: 0px 25px 25px -30px gray;

    transition: all 200ms;
}

#dropdown .title {
    color: #383838;
    font-size: 23px;
    font-weight: 700;

    margin-bottom: 15px;
}

#dropdown .link {
    color: rgba(0, 0, 0, 0.6);
    font-size: 18px;
    font-weight: 400;
    text-decoration: none;

    padding-bottom: 7px;
}
#header {
    position: fixed;    
    left: 0px;
    top: 0px;

    width: 100%;
    height: 140px;
    
    background-color: white;

    z-index: 100;
}

@media (max-width: 1320px) {
    #header {
        height: 70px;
    }
}

#header .border-panel {
    width: 100%;
    height: 100%;

    position: relative;

    display: flex;
    flex-direction: column;
    align-items: center;
}

#header .border-panel .border {
    position: absolute;
    left: 0px;
    bottom: 0px;
    width: 100%;
    height: 2px;

    background-color: rgba(0, 0, 0, 0.05);
}

@media (min-width: 1500px) {
    #header .border-panel .wrapper {
        display: flex;
        flex-direction: row;
        align-items: center;

        width: 1500px;
        height: 100%;
    }
}

@media (max-width: 1500px) {
    #header .border-panel .wrapper {
        display: flex;
        flex-direction: row;
        align-items: center;
    
        width: 100%;
        height: 100%;
    }
}

#header .border-panel .wrapper .logo-panel {
    height: 100%;

    display: flex;
    align-items: center;
    justify-content: center;

    padding: 20px 30px;
}

#header .border-panel .wrapper .logo-panel .logo {
    width: 250px;
    height: 100%;
    object-fit: contain;
}

@media (max-width: 1320px) {
    #header .border-panel .wrapper .logo-panel {
        justify-content: flex-start;
    
        padding: 0px 20px;
    }
    
    #header .border-panel .wrapper .logo-panel .logo {
        width: auto;
        height: 40px;
    }
}

#header .border-panel .wrapper .info-panel {
    display: flex;
    flex-direction: column;

    height: 100%;

    padding: 0px 40px 0px 0px;
}

@media (max-width: 1320px) {
    #header .border-panel .wrapper .info-panel {
        display: none;
    }
}

#header .border-panel .wrapper .info-panel .contact-panel {
    display: flex;
    flex-direction: row;
    align-items: center;

    flex: 1 1;

    padding: 0px 10px;
}

#header .border-panel .wrapper .info-panel .contact-panel .item {
    color: #383838;
    font-size: 16px;
    font-weight: 700;
}

#header .border-panel .wrapper .info-panel .contact-panel .break {
    width: 30px;
}

#header .border-panel .wrapper .info-panel .menu-panel {
    display: flex;
    flex-direction: row;
    align-items: center;
}

#header .border-panel .wrapper .info-panel .menu-panel .item {
    display: flex;
    flex-direction: row;
    align-items: center;

    height: 60px;

    padding: 10px;

    color: #383838;
    font-size: 16px;
    font-weight: 700;

    text-decoration: none;

    border-bottom: 2px solid transparent;

    transition: all 200ms;
}

#header .border-panel .wrapper .info-panel .menu-panel .item:hover {
    border-bottom: 2px solid #A161B3;
}

#header .border-panel .wrapper .info-panel .menu-panel .item ion-icon {
    margin-right: 15px;
}

#header .border-panel .wrapper .info-panel .menu-panel .divider {
    width: 2px;
    height: 16px;

    border-radius: 10px;

    margin: 0px 10px;

    background-color: rgba(0, 0, 0, 0.06);
}

