.popup-screen {
    position: fixed;
    left: 0px;
    top: 0px;

    width: 100%;
    width: -moz-available;
    width: -webkit-fill-available;
    width: fill-available;
    height: 100%;
    height: -moz-available;
    height: -webkit-fill-available;
    height: fill-available;

    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;

    background-color: rgba(0, 0, 0, 0.4);

    z-index: 11;
}

.popup-screen .popup {
    position: relative;

    display: flex;
    flex-direction: column;
    align-items: center;

    padding: 20px;

    background-color: white;
}

.popup-screen .popup .title {
    color: #383838;
    font-size: 25px;
    font-weight: 700;
    text-align: center;

    margin-bottom: 20px;
}

.popup-screen .popup .text {
    color: rgba(0, 0, 0, 0.5);
    font-size: 16px;
    font-weight: 400;
    text-align: center;
    line-height: 26px;

    margin-bottom: 10px;
}

.popup-screen .popup .field {
    color: black;
    font-size: 15px;
    font-weight: 400;

    padding: 8px;

    margin-bottom: 10px;

    width: 350px;

    border: 1px solid lightgray;
    outline: none;

    margin-bottom: 30px;
}

.popup-screen .popup .button-filled {
    
}

.popup-screen .popup ion-icon {
    position: absolute;
    right: 10px;
    top: 10px;

    width: 20px;
    height: 20px;

    color: #383838;

    cursor: pointer;
}

.popup-screen .popup .loader {
    width: 40px;
    height: 40px;

    border-radius: 50%;

    border: 7px solid #f3f3f3;
    border-top: 7px solid #A161B3;
    border-left: 7px solid #A161B3;

    -webkit-animation: spin 1s linear infinite; /* Safari */
    animation: spin 1500ms linear infinite;
}

@-webkit-keyframes spin {
    0% { -webkit-transform: rotate(0deg); }
    100% { -webkit-transform: rotate(360deg); }
}
  
@keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}
#admin-shop {
    width: 100%;

    background-color: white;
}

#admin-shop .content {
    width: 100%;

    display: flex;
    flex-direction: column;
    align-items: flex-start;

    padding: 50px;
}

@media (max-width: 800px) {
    #admin-shop .content {
        padding: 20px;
    }
}

#admin-shop .content .title {
    color: #383838;
    font-size: 40px;
    font-weight: 700;

    margin-bottom: 50px;
}

#admin-shop .content .section {
    color: #383838;
    font-size: 20px;
    font-weight: 700;

    width: 100%;

    padding-bottom: 5px;
    margin-bottom: 10px;

    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}

#admin-shop .content .text {
    color: rgba(0, 0, 0, 0.5);
    font-size: 16px;
    font-weight: 400;
    line-height: 26px;

    width: 50%;

    margin-bottom: 50px;
}

@media (max-width: 800px) {
    #admin-shop .content .text {
