#product {
    width: 100%;
}

#product .content {
    width: 100%;

    display: flex;
    flex-direction: row;
    align-items: flex-start;

    padding: 50px 100px;
    margin-bottom: 30px;
}

@media (max-width: 1300px) {
    #product .content {
        padding: 50px;
    }
}

@media (max-width: 800px) {
    #product .content {
        flex-direction: column-reverse;
        align-items: center;

        padding: 50px 20px;
    }
}

#product .content .image {
    width: 150px;
    object-fit: contain;

    margin: 0px 100px 0px 50px;
}

@media (max-width: 1300px) {
    #product .content .image {
        width: 150px;
        object-fit: contain;

        margin: 0px 50px;
    }
}

@media (max-width: 800px) {
    #product .content .image {
        width: 150px;
        object-fit: contain;

        margin: 70px 0px 0px 0px;
    }
}

#product .content .info-panel {
    display: flex;
    flex-direction: column;
    align-items: flex-start;

    flex: 1;
}

#product .content .info-panel .name {
    color: #383838;
    font-size: 40px;
    font-weight: 700;
}

#product .content .info-panel .label {
    color: rgba(0, 0, 0, 0.5);
    font-size: 20px;
    font-weight: 400;

    margin-bottom: 20px;
}

#product .content .info-panel .description {
    color: rgba(0, 0, 0, 0.5);
    font-size: 18px;
    font-weight: 400;
    line-height: 30px;
}

#product .content .info-panel .price-panel {
    display: flex;
    flex-direction: column;
}

#product .content .info-panel .price-panel .heading {
    color: #383838;
    color: rgba(0, 0, 0, 0.5);
    font-size: 20px;
    font-weight: 400;

    margin-bottom: 5px;

    display: flex;
    flex-direction: row;
    align-items: center;
}

#product .content .info-panel .price-panel .price {
    color: #383838;
    font-size: 25px;
    font-weight: 700;

    margin-bottom: 20px;

    display: flex;
    flex-direction: row;
    align-items: center;
}

#product .content .info-panel .price-panel .price ion-icon {
    margin-left: 10px;

    color: #A161B3;

    cursor: pointer;
}

#product .content .info-panel .buy-panel {
    display: flex;
    flex-direction: row;
    align-items: center;

    margin-bottom: 20px;
}

#product .content .info-panel .buy-panel .button-filled {
    margin-right: 20px;
}

#product .content .info-panel .controls {
    display: flex;
    flex-direction: row;
    align-items: center;

    height: 25px;

    margin: 10px 0px;
}

#product .content .info-panel .controls .button {
    width: 25px;
    height: 25px;

    display: flex;
    align-items: center;
    justify-content: center;

    color: #383838;
    font-weight: 700;

    cursor: pointer;

    transition: all 150ms;

    background-color: rgba(0, 0, 0, 0.12);
}

#product .content .info-panel .controls .button:hover {
    background-color: rgba(0, 0, 0, 0.2);
}

#product .content .info-panel .controls .amount {
    color: #383838;
    font-size: 15px;
    font-weight: 700;
    
    height: 100%;

    padding: 0px 10px;

    display: flex;
    align-items: center;

    background-color: rgba(0, 0, 0, 0.07);
}

#product .content .info-panel .sold-message {
    color: #A161B3;
    font-size: 25px;
    font-weight: 700;

    margin-bottom: 20px;
}